'use client';

import React, { useEffect, useState } from 'react';
import { Card } from '@/components/ui/card';
import { DollarLineIcon, TimeIcon, CheckCircleIcon, ErrorIcon } from '@/icons';
import { services } from '@/services/api';
import { AdminStats } from '@/services/api/adminStats.service';
import { toast } from 'react-hot-toast';
import { formatCurrency, formatDate } from '@/utils/format';
import { RevenueByMonthChart } from '@/components/dashboard/RevenueByMonthChart';
import { SalesByStatusChart } from '@/components/dashboard/SalesByStatusChart';

export default function AdminDashboardPage() {
  const [stats, setStats] = useState<AdminStats | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchStats = async () => {
      try {
        setLoading(true);
        const response = await services.adminStats.getStats();
        setStats(response.data);
      } catch (err) {
        toast.error('Erro ao carregar estatísticas do dashboard.');
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchStats();
  }, []);

  if (loading) {
    return (
      <div className="flex items-center justify-center h-96">
        <div className="h-16 w-16 animate-spin rounded-full border-4 border-solid border-primary border-t-transparent"></div>
      </div>
    );
  }

  if (!stats) {
    return null;
  }

  const planStats = stats.plans;
  const companyStats = stats.companies;
  const userBreakdown = stats.users.breakdown;
  const statusItems = planStats.statusDistribution.map((item) => ({
    status: item.status,
    total: item.total,
  }));

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-2xl font-bold text-gray-900 dark:text-white">Visão Geral do Sistema</h1>
          <p className="text-sm text-gray-500 dark:text-gray-400">
            Acompanhe as métricas de planos e empresas conectadas ao seu marketplace.
          </p>
        </div>
      </div>

      {/* Indicadores financeiros principais */}
      <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-4 gap-6">
        <Card className="p-6">
          <p className="text-sm font-medium text-gray-500 dark:text-gray-400">Receita total de planos</p>
          <p className="mt-2 text-3xl font-semibold text-gray-900 dark:text-white">
            {formatCurrency(planStats.totalRevenue)}
          </p>
          <p className="mt-1 text-xs text-gray-500 dark:text-gray-400">
            {planStats.paidPurchases} compras confirmadas
          </p>
        </Card>
        <Card className="p-6">
          <p className="text-sm font-medium text-gray-500 dark:text-gray-400">Receita pendente</p>
          <p className="mt-2 text-3xl font-semibold text-amber-600">
            {formatCurrency(planStats.pendingRevenue)}
          </p>
          <p className="mt-1 text-xs text-gray-500 dark:text-gray-400">
            {planStats.pendingPurchases} compras aguardando pagamento
          </p>
        </Card>
        <Card className="p-6">
          <p className="text-sm font-medium text-gray-500 dark:text-gray-400">Ticket médio dos planos</p>
          <p className="mt-2 text-3xl font-semibold text-gray-900 dark:text-white">
            {formatCurrency(planStats.averageTicket)}
          </p>
          <p className="mt-1 text-xs text-gray-500 dark:text-gray-400">
            {planStats.catalog} planos no catálogo ({planStats.activePlans} ativos)
          </p>
        </Card>
        <Card className="p-6">
          <p className="text-sm font-medium text-gray-500 dark:text-gray-400">Empresas conectadas</p>
          <p className="mt-2 text-3xl font-semibold text-gray-900 dark:text-white">
            {companyStats.total}
          </p>
          <div className="mt-3 grid grid-cols-2 gap-2 text-xs">
            <span className="inline-flex items-center gap-1 font-medium text-green-600">
              <CheckCircleIcon className="h-6 w-6" /> {companyStats.active} ativas
            </span>
            <span className="inline-flex items-center gap-1 font-medium text-red-500">
              <ErrorIcon className="h-6 w-6" /> {companyStats.expired} vencidas
            </span>
            <span className="inline-flex items-center gap-1 font-medium text-amber-500">
              <TimeIcon className="h-6 w-6" /> {companyStats.expiring} vencendo
            </span>
          </div>
        </Card>
      </div>

      <div className="grid grid-cols-1 xl:grid-cols-5 gap-6">
        <div className="xl:col-span-3">
          <RevenueByMonthChart data={planStats.monthlyRevenue.map((item) => ({
            label: item.label.toUpperCase(),
            revenue: item.revenue,
          }))} />
        </div>
        <div className="xl:col-span-2 space-y-6">
          <SalesByStatusChart items={statusItems} />
          <Card className="p-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Visão rápida</h3>
            <dl className="space-y-3 text-sm text-gray-600 dark:text-gray-300">
              <div className="flex justify-between">
                <dt>Planos ativos</dt>
                <dd className="font-medium text-gray-900 dark:text-white">{planStats.activePlans}</dd>
              </div>
              <div className="flex justify-between">
                <dt>Planos inativos</dt>
                <dd className="font-medium">{planStats.inactivePlans}</dd>
              </div>
              <div className="flex justify-between">
                <dt>Empresas com mais clientes</dt>
                <dd className="font-medium">{companyStats.topByClients[0]?.nome ?? '—'}</dd>
              </div>
            </dl>
          </Card>
        </div>
      </div>

      <div className="grid grid-cols-1 xl:grid-cols-2 gap-6">
        <Card className="p-6">
          <h2 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Top planos por receita</h2>
          <div className="space-y-3">
            {planStats.topPlans.map((plano) => (
              <div
                key={plano.nome}
                className="flex items-center justify-between rounded-lg border border-gray-200 bg-gray-50 px-4 py-3 dark:border-gray-700 dark:bg-white/[0.02]"
              >
                <div>
                  <p className="font-medium text-gray-900 dark:text-white">{plano.nome}</p>
                  <p className="text-xs text-gray-500 dark:text-gray-400">
                    {plano.purchases} vendas · Ticket médio {formatCurrency(plano.averageTicket)}
                  </p>
                </div>
                <p className="text-sm font-semibold text-gray-900 dark:text-white">
                  {formatCurrency(plano.revenue)}
                </p>
              </div>
            ))}
          </div>
        </Card>

        <Card className="p-6">
          <h2 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Empresas que mais faturam</h2>
          <div className="space-y-3">
            {planStats.topCompanies.map((empresa) => (
              <div
                key={empresa.nome}
                className="flex items-center justify-between rounded-lg border border-gray-200 bg-gray-50 px-4 py-3 dark:border-gray-700 dark:bg-white/[0.02]"
              >
                <div>
                  <p className="font-medium text-gray-900 dark:text-white">{empresa.nome}</p>
                  <p className="text-xs text-gray-500 dark:text-gray-400">
                    {empresa.purchases} compras de planos
                  </p>
                </div>
                <p className="text-sm font-semibold text-gray-900 dark:text-white">
                  {formatCurrency(empresa.revenue)}
                </p>
              </div>
            ))}
          </div>
        </Card>
      </div>

      <div className="grid grid-cols-1 xl:grid-cols-2 gap-6">
        <Card className="p-6">
          <h2 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Compras recentes de planos</h2>
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700 text-sm">
              <thead className="bg-gray-50 dark:bg-white/[0.05]">
                <tr>
                  <th className="px-3 py-2 text-left font-semibold text-gray-600 dark:text-gray-300">Empresa</th>
                  <th className="px-3 py-2 text-left font-semibold text-gray-600 dark:text-gray-300">Plano</th>
                  <th className="px-3 py-2 text-left font-semibold text-gray-600 dark:text-gray-300">Valor</th>
                  <th className="px-3 py-2 text-left font-semibold text-gray-600 dark:text-gray-300">Status</th>
                  <th className="px-3 py-2 text-left font-semibold text-gray-600 dark:text-gray-300">Data</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200 dark:divide-gray-700">
                {planStats.recentPurchases.map((compra) => (
                  <tr key={compra.id} className="bg-white dark:bg-white/[0.015]">
                    <td className="px-3 py-2 text-gray-800 dark:text-gray-100">{compra.empresa}</td>
                    <td className="px-3 py-2 text-gray-600 dark:text-gray-300">{compra.plano}</td>
                    <td className="px-3 py-2 font-medium text-gray-900 dark:text-white">
                      {formatCurrency(compra.valor)}
                    </td>
                    <td className="px-3 py-2">
                      <span
                        className={`rounded-full px-2.5 py-1 text-xs font-semibold ${
                          compra.status === 'PAGO'
                            ? 'bg-emerald-100 text-emerald-700 dark:bg-emerald-500/10 dark:text-emerald-300'
                            : 'bg-amber-100 text-amber-700 dark:bg-amber-500/10 dark:text-amber-300'
                        }`}
                      >
                        {compra.status === 'PAGO' ? 'Pago' : 'Pendente'}
                      </span>
                    </td>
                    <td className="px-3 py-2 text-gray-500 dark:text-gray-400">
                      {formatDate(compra.dataPagamento ?? compra.createdAt)}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </Card>

        <Card className="p-6">
          <h2 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Distribuição de usuários</h2>
          <div className="grid grid-cols-2 gap-4 text-sm">
            {Object.entries(userBreakdown).map(([role, total]) => (
              <div
                key={role}
                className="rounded-lg border border-gray-200 bg-gray-50 px-4 py-3 text-gray-700 dark:border-gray-700 dark:bg-white/[0.02] dark:text-gray-300"
              >
                <p className="text-xs uppercase text-gray-500 dark:text-gray-400">{role}</p>
                <p className="mt-1 text-xl font-semibold text-gray-900 dark:text-white">{total}</p>
              </div>
            ))}
          </div>
        </Card>
      </div>
    </div>
  );
}